<template>

  <div class="header">
    <div class="header-container" :class="{'scroll-nav' : scrollNav}">
    <div class="header__hide-box">
<!--      <Navbar @click="toggleNav"/>-->
    </div>
    <div class="header__logo">
      <router-link to="/" >
        <img src="../assets/image/1010_APOTHECARY_LOGO-OK-01.png" alt>
      </router-link>
    </div>
    <div class="header__mini-bar">
      <div class="mini-user">
        <button class="btn mini-user__img" >
        <font-awesome-icon :icon="['fas', 'user']"/>
        </button>
      </div>
      <div class="mini-cart">
        <div class="container">
          <div class="cartIcon">
        <button class="btn cartIcon__img" @click="sideCartToggle">
        <font-awesome-icon :icon="['fas', 'bag-shopping']"/>
        </button>
        <div class="icon-num" v-if="cartLen !== 0">
          <p>{{ cartLen }}</p>
        </div>
        <div class="icon-null" v-else>
          <p>{{ cartLen }}</p>
        </div>
          </div>
        <transition name="sideCart" v-show="sideCart">
          <div class="cart-wrap" >
            <div class="cart-container">
              <div class="cart-header">
                <h2>
                  <font-awesome-icon :icon="['fas', 'cart-shopping']"/>
                  
                </h2>
                <a href="#" @click.prevent="sideCartToggle">
                  <font-awesome-icon :icon="['fas', 'xmark']"/>
                </a>
              </div>
              <h3 v-if="cartLen === 0">!</h3>
              <div class="cart-item" v-for="item in cart.carts" :key="item.id">
                <div class="cart-photo">
                  <img :src="item.product.imageUrl" alt="">
                </div>
                <div class="cart-text">
                  <h3> {{ item.product.title }} </h3>
                  <div class="num">
                    <h4> {{ item.qty }} </h4>
                    <h4>NT {{ $filters.currency(item.total) }} </h4>
                  </div>
                </div>
                <button type="button" class="cart-del"  :disabled="this.status.loadingItem === item.id" @click="removeCartItem(item.id)">
                  <font-awesome-icon :icon="['fas', 'spinner']" v-if="this.status.loadingItem === item.id"/>
                  <font-awesome-icon :icon="['fas', 'trash-can']" v-else/>
                </button>
              </div>
              <div class="cart-footer" v-if="cartLen !== 0">
                <h4></h4>
                <h5>$ {{ $filters.currency(cart.total) }}</h5>
              </div>
              <div class="cart-checkout" v-if="cartLen !== 0">
                <router-link to="/cart/cartPage">
                  <font-awesome-icon :icon="['fas', 'cart-shopping']" />
                  
                </router-link>
              </div>
            </div>
          </div>
        </transition>
        </div>
      </div>
    </div>
    </div>
    <div class="nav-container" >
      <nav class="scroll-menu" v-show="!mobile">
        <ul class="navbar-nav me-auto">
          <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
          <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
          <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
          <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
          <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
        </ul>
      </nav>
      <div class="hamburger-icon" :class="{'hamburger-icon-active' : mobileNav}" @click="toggleNav" v-show="mobile">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <transition name="mobile-nav">
        <nav class="mobile-menu" v-show="mobileNav">
          <ul class="navbar-nav">
            <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
            <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
            <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
            <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
            <li class="nav-item"><router-link to="/" class="nav-link" ></router-link></li>
          </ul>
        </nav>
      </transition>
    </div>
  </div>
</template>

<script>
import emitter from '@/methods/getEmitter'
// import Navbar from '@/components/Navbar'

export default {
  name: 'Navbar-view',
  data () {
    return {
      cartLen: 0,
      cart: {},
      products: [],
      product: {},
      sideCart: '',
      status: {
        loadingItem: ''
      },
      navbarOpen: false,
      scrollNav: '',
      mobileNav: '',
      mobile: '',
      screenWidth: ''
    }
  },
  components: {
    // Navbar
  },
  methods: {
    getCartList () {
      const getCartApi = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.$http.get(getCartApi)
        .then((res) => {
          // console.log('getCart:', res.data.data.carts)
          this.cart = res.data.data
          this.cartLen = res.data.data.carts.length
        })
    },
    removeCartItem (id) {
      this.status.loadingItem = id
      const deleteItemApi = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${id}`
      this.$http.delete(deleteItemApi)
        .then((res) => {
          // console.log('deleteItem', res)
          this.$httpMsgState(res, '')
          this.status.loadingItem = ''
          this.getCart()
        })
    },
    sideCartToggle () {
      this.sideCart = !this.sideCart
      // console.log(this.sideCart)
    },
    toggleNav () {
      this.mobileNav = !this.mobileNav
      // console.log('navbarOpen', this.navbarOpen)
    },
    screenSize () {
      this.screenWidth = window.innerWidth
      // console.log('screenWidth', this.screenWidth)
      if (this.screenWidth <= 768) {
        this.mobile = true
        // console.log('mobile', this.mobile)
        return
      }
      this.mobile = false
      this.mobileNav = false
    },
    updateScroll () {
      const scrollPosition = window.scrollY
      console.log(scrollPosition)
      if (scrollPosition > 50) {
        this.scrollNav = true
        return
      }
      this.scrollNav = false
    }
    // logout () {
    //   const logoutApi = `${process.env.VUE_APP_API}logout`
    //   // console.log(logoutApi)
    //   this.$http.post(logoutApi, this.user)
    //     .then((res) => {
    //       if (res.data.success) {
    //         // console.log(res)
    //         this.$router.push('/login')
    //       }
    //     })
    // }
  },
  created () {
    this.getCartList()
    this.screenSize()
    emitter.on('updateCart', (data) => {
      this.cart = data
    })
    window.addEventListener('resize', this.screenSize)
  },
  mounted () {
    window.addEventListener('scroll', this.updateScroll)
  }
}
</script>

<style lang="scss" scoped>
@import "src/assets/helpers/customVariables";
@import "src/assets/css/nabar";
@import "src/assets/css/miniCart";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header{
  width: 100%;
  height: auto;
  margin-bottom: 0;
  z-index: 900;
  background: white;
  display: flex;
  flex-direction: column;
  position: fixed;
  @media screen and (max-width: 768px) {
    height: 70px;
  }
  &__hide-box {
    width: 30%;
    height: 110px;
    margin: 0 auto;
  }
  .header-container {
    display: flex;
    justify-content: center;
    @media screen and (max-width: 768px) {
      height: 70px;
    }
  }
  &__logo {
    width: 40%;
    transition: all .3s;
    margin: 0 auto;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 274px;
        height: 110px;
        aspect-ratio: auto 274 / 110;
        object-fit: contain;
        max-height: inherit;
        transition: height .3s ease-in-out;
        //display: block;
        //margin: 0 auto;
        @media screen and (max-width: 768px) {
          width: 70%;
          margin: 0 auto;
        }
      }
    }
  }
  .scroll-nav {
    height: 70px;
  }
  &__mini-bar {
    width: 30%;
    height: 110px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    .cartIcon, .mini-user {
      @media screen and (max-width: 768px) {
        width: 20%;
        height: 100px;
        margin: 0 5px;
      }
      &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        border: none;
        margin: 0 10px;
        font-size: 2.2em;
        //color: white;
        &:hover {
          transform: scale(1.05);
        }
      }
    }
    .mini-cart {
      position: relative;
      .icon-num,.icon-null {
        position: absolute;
        right: 25px;
        top: 8px;
        width: 21px;
        height: 21px;
        z-index: 2;
        border: none;
        background-color: #CB4042;
        padding: 6px;
        color: white;
        border-radius: 50%;
        text-align: center;
        opacity: 75%;
        @media screen and (max-width: 768px) {
          right: 30px;
        }
        p {
          font-size: 1em;
          position: absolute;
          top: -1px;
          left: 6px ;
        }
      }
    }
  }
}

</style>
