import emitter from '@/methods/getEmitter'

export default function (res, title = '') {
  if (res.data.success) {
    emitter.emit('push-message', {
      style: 'success',
      title: `${title}`
    })
  } else {
    // 
    const message = typeof res.data.message === 'string' ? [res.data.message] : res.data.message
    emitter.emit('push-message', {
      style: 'danger',
      title: `${title}`,
      content: message.join('') // join
    })
  }
}
