<template>
  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>{{ tempProduct.title ? '' : '' }}</span>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="image"
                  placeholder=""
                  v-model="tempProduct.imageUrl"
                >
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"> 
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="uploadFile"
                >
              </div>
              <img class="img-fluid" alt="" :src="tempProduct.imageUrl">
              <!--  -->
              <div class="mt-5" v-if="tempProduct.images">
                <div class="mb-3 input-group" v-for="(image, key) in tempProduct.images" :key="key">
<!--                  <label class="form-label"></label>-->
                  <input type="url" class="form-control form-control" placeholder="" v-model="tempProduct.images[key]">
                  <button type="button" class="btn btn-close mt-2" @click="tempProduct.images.splice(key, 1)">

                  </button>
                </div>
                <div v-if="tempProduct.images[tempProduct.images.length - 1] || !tempProduct.images.length">
                  <button class="btn btn-outline-primary btn-sm d-block w-100"
                          @click="tempProduct.images.push('')">
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  placeholder=""
                  v-model="tempProduct.title"
                >
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label"></label>
                  <input
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempProduct.category"
                  >
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="unit"
                    placeholder=""
                    v-model="tempProduct.unit"
                  >
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="origin_price"
                    placeholder=""
                    v-model="tempProduct.origin_price"
                  >
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="price"
                    placeholder=""
                    v-model="tempProduct.price"
                  >
                </div>
              </div>
              <hr>

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="description"
                  placeholder=""
                  v-model="tempProduct.description"
                ></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="content"
                  placeholder=""
                  v-model="tempProduct.content"
                ></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" :true-value="1" :false-value="0" id="is_enabled" v-model="tempProduct.is_enabled">
                  <label class="form-check-label" for="is_enabled">
                    
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary"
                  data-bs-dismiss="modal">
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="$emit('update-product',tempProduct)"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin'

export default {
  name: 'ProductModal',
  // ,
  // props,$emit()
  props: {
    product: {
      type: Object,
      // 
      default () {
        return {}
      }
    }
  },
  watch: {
    product () {
      this.tempProduct = this.product
      // 
      if (!this.tempProduct.images) {
        this.tempProduct.images = []
      }
    }
  },
  data () {
    return {
      modal: {},
      tempProduct: {}
    }
  },
  methods: {
    uploadFile () {
      const uploadFile = this.$refs.fileInput.files[0]
      // console.dir(uploadFile)
      const formData = new FormData()
      formData.append('file-to-upload', uploadFile)
      const fileUploadUrl = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      const urlUploadPlace = this.$refs.fileInput.id
      // console.log(fileUploadUrl)
      this.$http.post(fileUploadUrl, formData)
        .then((res) => {
          // console.log(res.data)
          if (res.data.success) {
            this.tempProduct.imageUrl = res.data.imageUrl
            document.getElementById(urlUploadPlace).value = ''
          } else if (!res.data.success) {
            this.$httpMsgState(res, '')
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }
  },
  mixins: [modalMixin]
}
</script>

<style scoped>

</style>
